.nav-item {
    width: 40px;
    height: 40px;
    background-color: #fff;
    border: 0;
    list-style: none;
}

.nav-item--icon--check-status {
    background-image: url(./static/check-status.svg);
}

.nav-item--icon--burger {
    background-image: url(./static/burger.svg);
}

.nav-item--icon--call {
    background-image: url(./static/call.svg);
    display: none;
}

@media (min-width: 768px) {
    .nav-item--icon--call {
        display: block;
    }
}

.nav-item--icon--chat {
    background-image: url(./static/chat.svg);
    display: none;
}

@media (min-width: 768px) {
    .nav-item--icon--chat {
        display: block;
    }
}

.nav-item--icon--profile {
    background-image: url(./static/profile.svg);
    display: none;
}

@media (min-width: 768px) {
    .nav-item--icon--profile {
        display: block;
    }
}

.nav-item--icon--repair {
    background-image: url(./static/repair.svg);
}